<template>
    <v-container>
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon color="primary"
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.replace()">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{changeEmailText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card class="pa-5" style="margin-top: 60px;">
            <v-form v-model="validForm">
                <v-text-field
                        v-model="email"
                        :rules="[rules.required, rules.email]"
                        :label="emailText"
                        required
                ></v-text-field>
            </v-form>
            <v-btn block color="primary" style="margin-top: 10px" @click="updateEmail" :disabled="buttonIsDisabled">
                {{updateText}}
            </v-btn>
        </v-card>

        <v-dialog
                v-model="dialog"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <p style="margin-bottom: 7px">{{yourEmailHasBeenUpdatedText}}</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="$router.go(-2)">{{continueText}}</v-btn>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import Constants from "../../store/Constants";
    import {mapActions} from "vuex";

    export default {
        name: "SettingsAccountEditEmail",
        data() {
            return {
                buttonIsDisabled: false,
                validForm: false,
                snackbar: false,
                snackbarText: null,
                dialog: false,
                rules: {
                    required: value => !!value || Constants.RULE_TEXT_MANDATORY_FIELD,
                    email: value => {
                        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                        return pattern.test(value) || Constants.TEXT_EMAIL_NOT_VALID
                    },
                },
                changeEmailText: Constants.TEXT_CHANGE_EMAIL,
                emailText: Constants.TEXT_EMAIL_DIRECTION,
                email: this.$store.getters['user/getEmail'],
                closeText: Constants.TEXT_CLOSE,
                continueText: Constants.TEXT_TO_CONTINUE,
                updateText: Constants.TEXT_UPDATE,
                yourEmailHasBeenUpdatedText: Constants.TEXT_YOUR_EMAIL_HAS_BEEN_UPDATED

            }
        },
        methods: {
            ...mapActions('user', ['updateAccountEmail']),

            updateEmail: function () {
                let self = this;
                if(self.email != self.$store.getters['user/getEmail']){
                    let resultUpdateAccountEmail = self.updateAccountEmail(self.email);

                    resultUpdateAccountEmail.then(() => {
                        self.dialog = true;
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }
                else{
                    self.$router.replace({name: 'settingsAccount'});
                }

            }
        }
    }
</script>

<style scoped>

</style>